body {
  margin: 0;
  background-color: #F4F6F9;
}

.slds-flex_column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slds-img-width_15rem {
  width: 15rem
}

.slds-heigh_100vh {
  height: 100vh;
}

.slds-login-card-body {
  width: 20rem;
  height: 12rem;
}

.slds-notification {
  animation: notificationup 0.2s ease-in-out
}



/* Animations */

@keyframes notificationup {
  0%   {
    transform: translate(0, 30px);
  }
  70% {
    transform: translate(0, -10px);
  }
  100% {
    transform: translate(0, 0);
  }
}